<template>
  <div class="grid p-fluid">
    <div class="col-12 lg:col-6">
      <div class="card">
        <div ref="kline" id="myChart" class="myChart"></div>
      </div>
    </div>

    <div class="col-12 lg:col6">
      <div class="card">
        <h5>Frozen Columns</h5>
        <DataTable
          :value="customer2"
          :scrollable="true"
          scrollHeight="800px"
          :loading="loading2"
          scrollDirection="both"
          class="mt-3"
        >
          <Column field="date" header="" :style="{ width: '100px' }" :frozen="idFrozen"></Column>
          <Column field="thirdup" header="" :style="TabColStyleObj" frozen></Column>
          <Column field="secondup" :header=titleWithKey(1) :style="TabColStyleObj"></Column>
          <Column field="mainup" :header="titleWithKey(2)" :style="TabColStyleObj"></Column>
          <Column field="maindown" :header=titleWithKey(3) :style="TabColStyleObj" frozen></Column>
          <Column field="seconddown" :header=titleWithKey(4) :style="TabColStyleObj"></Column>
          <Column field="thirddown" header="" :style="TabColStyleObj"></Column>
        </DataTable>
      </div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts/core";
import {
  TooltipComponent,
  GridComponent,
  LegendComponent,
  DataZoomComponent,
} from "echarts/components";

import { CandlestickChart, LineChart } from "echarts/charts";
import { UniversalTransition } from "echarts/features";
import { CanvasRenderer } from "echarts/renderers";
import * as axios from "axios";

echarts.use([
  TooltipComponent,
  GridComponent,
  LegendComponent,
  DataZoomComponent,
  CandlestickChart,
  LineChart,
  CanvasRenderer,
  UniversalTransition,
]);

export default {
  props: {
    number: String, //
  },

  mounted () {
    axios
    //https://ifzq.gtimg.cn/appstock/app/kline/mkline?param=sh601658,m60,,320,qfq
      .get("/txapi/fqkline/get?param=sh601658,day,,,500,qfq")
      .then((response) => {
        this.initChart(response.data.data['sh601658'].qfqday)
        this.initTable(response.data.data['sh601658'].qfqday)
      });


    //setInterval(() => {
    //  this.counter++;
    //@ts-check}, 1000);
  },

  data () {
    return {
      historydata: null,
      TabColStyleObj: {
        width: "100px",
      },
      customer2: null,
      keys:[null,null,null,null,null,null]
    };
  },

  methods: {
    initChart (rawData) {
      debugger
      var chartDom = document.getElementById("myChart");
      var myChart = echarts.init(chartDom);
      var option;

      // prettier-ignore

      function calculateMA (dayCount, data) {
        var result = [];
        for (var i = 0, len = data.length; i < len; i++) {
          if (i < dayCount) {
            result.push("-");
            continue;
          }
          var sum = 0;
          for (var j = 0; j < dayCount; j++) {
            sum += +data[i - j][1];
          }
          result.push(sum / dayCount);
        }
        return result;
      }
      const dates = rawData.map(function (item) {
        return item[0];
      });
      const data = rawData.map(function (item) {
        return [item[1], item[2], item[4], item[3]];
      });
      option = {
        legend: {
          data: ["K", "MA5", "MA10", "MA20", "MA60"],
          inactiveColor: "#777",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            animation: false,
            type: "cross",
            lineStyle: {
              color: "#376df4",
              width: 2,
              opacity: 1,
            },
          },
        },
        xAxis: {
          type: "category",
          data: dates,
          axisLine: { lineStyle: { color: "#8392A5" } },
        },
        yAxis: {
          scale: true,
          axisLine: { lineStyle: { color: "#8392A5" } },
          splitLine: { show: false },
        },
        grid: {
          bottom: 80,
        },
        dataZoom: [
          {
            textStyle: {
              color: "#8392A5",
            },
            handleIcon:
              "path://M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z",
            dataBackground: {
              areaStyle: {
                color: "#8392A5",
              },
              lineStyle: {
                opacity: 0.8,
                color: "#8392A5",
              },
            },
            brushSelect: true,
          },
          {
            type: "inside",
          },
        ],
        series: [
          {
            type: "candlestick",
            name: "Day",
            data: data,
            itemStyle: {
              color: "#FD1050",
              color0: "#0CF49B",
              borderColor: "#FD1050",
              borderColor0: "#0CF49B",
            },
          },
          {
            name: "MA5",
            type: "line",
            data: calculateMA(5, data),
            smooth: true,
            showSymbol: false,
            lineStyle: {
              width: 1,
            },
          },
          {
            name: "MA10",
            type: "line",
            data: calculateMA(10, data),
            smooth: true,
            showSymbol: false,
            lineStyle: {
              width: 1,
            },
          },
          {
            name: "MA20",
            type: "line",
            data: calculateMA(20, data),
            smooth: true,
            showSymbol: false,
            lineStyle: {
              width: 1,
            },
          },
          {
            name: "MA60",
            type: "line",
            data: calculateMA(60, data),
            smooth: true,
            showSymbol: false,
            lineStyle: {
              width: 1,
            },
          },
        ],
      };

      option && myChart.setOption(option);
    },

    initTable (rawData) {
      const dates = rawData.map(function (item) {
        return item[0];
      });

      const closes = rawData.map(function (item) {
        return Number(item[2]);
      });
      let minValue = Math.min(...closes)
      let maxValue = Math.max(...closes)
      let minIndex = closes.indexOf(minValue)
      let maxIndex = closes.indexOf(maxValue)

      //let minIndex = closes.indexOf(Math.min(closes))
      //let maxIndex = closes.indexOf(Math.max(closes))

      //true    false 
      let direc = maxIndex > minIndex
      let beginIndex = direc ? minIndex : maxIndex
      let beginValue = direc ? minValue : maxValue

      var ValueType = {
        THIRDUP: 0,
        SECONDUP: 1,
        MAINUP: 2,
        MAINDOWN: 3,
        SECONDDOWN: 4,
        THIRDDOWN: 5,
      }

      var currentValueType = direc ? ValueType.MAINDOWN : ValueType.MAINUP
      var lastValueType = direc ? ValueType.MAINDOWN : ValueType.MAINUP

      //
      //var keys = [[0,null],[0,null],[0,null],[0,null],[0,null],[0,null]]
      //keys[currentValueType] = [beginIndex,beginValue]
      var keys = [null, null, null, null, null, null]
      keys[currentValueType] = beginValue

      var tableData = new Array()

      let tableItemData = {
        date: dates[beginIndex],
        thirdup: null,
        secondup: null,
        mainup: null,
        maindown: null,
        seconddown: null,
        thirddown: null,
      }
      direc ? tableItemData.maindown = closes[beginIndex] : tableItemData.mainup = closes[beginIndex]
      tableData.push(tableItemData)


      let bigTurnPoint = 0.06
      let smallTurnPoint = 0.03

      function insertNewData (i) {
        let tableItemData = {
          date: dates[i],
          thirdup: null,
          secondup: null,
          mainup: null,
          maindown: null,
          seconddown: null,
          thirddown: null,
        }

        switch (currentValueType) {
          case ValueType.THIRDUP:
            tableItemData.thirdup = closes[i]
            break;
          case ValueType.SECONDUP:
            tableItemData.secondup = closes[i]
            break;
          case ValueType.MAINUP:
            tableItemData.mainup = closes[i]
            break;
          case ValueType.MAINDOWN:
            tableItemData.maindown = closes[i]
            break;
          case ValueType.SECONDDOWN:
            tableItemData.seconddown = closes[i]
            break;
          case ValueType.THIRDDOWN:
            tableItemData.thirddown = closes[i]
            break;
        }
        tableData.push(tableItemData)
      }

      function getLastValue () {
        var ret = null
        switch (currentValueType) {
          case ValueType.THIRDUP:
            ret = tableData[tableData.length - 1].thirdup
            break;
          case ValueType.SECONDUP:
            ret = tableData[tableData.length - 1].secondup
            break;
          case ValueType.MAINUP:
            ret = tableData[tableData.length - 1].mainup
            break;
          case ValueType.MAINDOWN:
            ret = tableData[tableData.length - 1].maindown
            break;
          case ValueType.SECONDDOWN:
            ret = tableData[tableData.length - 1].seconddown
            break;
          case ValueType.THIRDDOWN:
            ret = tableData[tableData.length - 1].thirddown
            break;
        }
        return ret
      }

      for (let i = beginIndex + 1; i < closes.length - 1; i++) {

        let lastValue = getLastValue()

        switch (currentValueType) {
          case ValueType.THIRDUP: {
            if (closes[i] > lastValue) {
              //
              if (keys[ValueType.SECONDUP] != null && closes[i] > keys[ValueType.SECONDUP]) {
                //
                if ((closes[i] - keys[ValueType.SECONDUP]) / keys[ValueType.SECONDUP] > smallTurnPoint) {
                  //3
                  lastValueType = currentValueType
                  currentValueType = ValueType.MAINUP
                } else {
                  lastValueType = currentValueType
                  currentValueType = ValueType.SECONDUP
                }
              }
              insertNewData(i)
            }
            break;
          }
          case ValueType.SECONDUP: {
            if (closes[i] > lastValue) {
              //
              if (keys[ValueType.MAINUP] != null && keys[ValueType.MAINUP] && closes[i] > keys[ValueType.MAINUP]) {
                //
                lastValueType = currentValueType
                currentValueType = ValueType.MAINUP
              }
              else if (keys[currentValueType] != null && (closes[i] - keys[currentValueType]) / keys[currentValueType] > smallTurnPoint) {
                //3
                lastValueType = currentValueType
                currentValueType = ValueType.MAINUP
              }
              insertNewData(i)
            }
            else if ((lastValue - closes[i]) / lastValue > bigTurnPoint) {
              if (keys[ValueType.SECONDDOWN] != null && lastValueType == ValueType.SECONDDOWN && closes[i] > keys[ValueType.SECONDDOWN]) {
                //6h
                //keys[currentValueType] = closes[i]
                lastValueType = ValueType.SECONDUP
                currentValueType = ValueType.THIRDDOWN
              }
              else {
                //
                keys[currentValueType] = lastValue
                lastValueType = ValueType.SECONDUP
                currentValueType = ValueType.SECONDDOWN
              }
              insertNewData(i)
            }
            break;
          }
          case ValueType.MAINUP: {
            if (closes[i] > lastValue) {
              //
              insertNewData(i)
            }
            else if ((lastValue - closes[i]) / lastValue > bigTurnPoint) {
              //
              keys[currentValueType] = lastValue
              lastValueType = currentValueType
              currentValueType = ValueType.SECONDDOWN
              insertNewData(i)
            }
            break;
          }
          case ValueType.MAINDOWN: {
            if (closes[i] < lastValue) {
              //
              insertNewData(i)
            }
            else if ((closes[i] - lastValue) / lastValue > bigTurnPoint) {
              //
              keys[currentValueType] = lastValue
              lastValueType = ValueType.MAINDOWN
              currentValueType = ValueType.SECONDUP
              insertNewData(i)
            }
            break;
          }
          case ValueType.SECONDDOWN: {
            if (closes[i] < lastValue) {
              //
              if (keys[ValueType.MAINDOWN] != null && closes[i] < keys[ValueType.MAINDOWN]) {
                //
                lastValueType = currentValueType
                currentValueType = ValueType.MAINDOWN
              }
              else if (keys[currentValueType] != null && (keys[currentValueType] - closes[i]) / keys[currentValueType] > smallTurnPoint) {
                //3
                lastValueType = currentValueType
                currentValueType = ValueType.MAINDOWN
              }
              insertNewData(i)
            }
            else if ((closes[i] - lastValue) / lastValue > bigTurnPoint) {
              //
              if (keys[ValueType.SECONDUP] != null && lastValueType == ValueType.SECONDUP && closes[i] < keys[ValueType.SECONDUP]) {
                //6g
                //keys[currentValueType] = closes[i]
                lastValueType = ValueType.SECONDDOWN
                currentValueType = ValueType.THIRDUP
              }
              else {
                keys[currentValueType] = lastValue
                lastValueType = ValueType.SECONDDOWN
                currentValueType = ValueType.SECONDUP
              }
              insertNewData(i)
            }
            break;
          }
          case ValueType.THIRDDOWN: {
            if (closes[i] < lastValue) {
              //
              if (keys[ValueType.SECONDDOWN] != null && closes[i] < keys[ValueType.SECONDDOWN]) {
                if ((keys[ValueType.SECONDDOWN] - closes[i]) / keys[ValueType.SECONDDOWN] > smallTurnPoint) {
                  //3
                  lastValueType = ValueType.SECONDDOWN
                  currentValueType = ValueType.MAINDOWN
                } else {
                  //
                  lastValueType = currentValueType
                  currentValueType = ValueType.SECONDDOWN
                }
              }
              insertNewData(i)
            }
            break;
          }
        }
      }

      debugger
      this.customer2 = tableData
      this.keys = keys
    },
    
    titleWithKey(keyType) {
      var title = null
      switch(keyType){
        case 1:
          title  = '' + this.keys[1]
          break
        case 2:
          title =  '' + this.keys[2]
          break
        case 3:
          title =  '' + this.keys[3]
          break
        case 4:
          title =  '' + this.keys[4]
          break
      }
      return title
    }
  },

};
</script>

<style scoped lang="scss">
.myChart {
  height: 400px;
}
</style>
